* {
    padding: 0;
    margin: 0;
    border: 0; }


*,*::before, *:after {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box; }

a:focus,a:active {
    outline: none; }


nav,footer,header,aside {
    display: block; }

html,body {
    height: 100%;
    width: 100%;
    font-size: 100%;
    line-height: 1;
    font-size: 14px;
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%; }

input,button,textarea {
    font-family: inherit; }

input::-ms-clear {
    display: none; }

a, a:visited {
    text-decoration: none; }

a:hover {
    text-decoration: none; }

ul li {
    list-style: none; }
img {
    vertical-align: top; }

body {
    color: $commonFontColor;
    width: 1920px;
    background: $backgroundColor;
    font-family: $commonFont;
    font-weight: 400; }

h1,h2,h3,h4,h5,h6 {
    margin: 0;
    font-family: $titlesFont;
    font-size: inherit;
    font-weight: 700;
    color: $titlesFontColor; }

a {
    font-family: $commonFont;
    color: $mainColor;
    font-weight: 400; }

button {
    font-family: $commonFont;
    font-weight: 700;
    cursor: pointer;
    border-radius: 15px;

    &:hover,
    &:focus {
        opacity: 0.8; }

    &:active {
        opacity: 0.6; } }



button::-moz-focus-inner {
    padding: 0;
    border: 0; }

/*
 * Hide only visually, but have it available for screenreaders: h5bp.com/v
 */

.visuallyhidden {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px; }

/*
 * Extends the .visuallyhidden class to allow the element to be focusable
 * when navigated to via the keyboard: h5bp.com/p
 */

.visuallyhidden.focusable:active, .visuallyhidden.focusable:focus {
    clip: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    position: static;
    width: auto; }
